import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { AxiosMasterGet } from "../../../axios";
import { getToday } from "../../../components/notification/function";
import {
  ReanderField,
  RenderFieldGroup,
} from "../../../components/notification/notification";

class HeadServiceLuar extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  getServiceLuar() {
    alert("ITS WORKED");
  }
  componentDidMount() {
    this.props.change("tanggal", getToday());
    AxiosMasterGet("kirim-service-luar/generate/no-trx").then((res) =>
      this.props.change("no_service_kirim", res && res.data[0].no_service_kirim)
    );
  }
  render() {
    return (
      <div className="row">
        <div className="col-lg-4">
          <Field
            name="no_service_kirim"
            component={RenderFieldGroup}
            type="text"
            label="Nomor Service Kirim"
            placeholder="Masukan Nomor Service Kirim"
            handleClick={(data) => this.getServiceLuar(data)}
          />
        </div>
        <div className="col-lg-4">
          <Field
            name="tanggal"
            component={ReanderField}
            type="date"
            label="Tanggal"
            placeholder="Masukan Tanggal"
          />
        </div>
        <div className="col-lg-4"></div>
      </div>
    );
  }
}

HeadServiceLuar = reduxForm({
  form: "HeadServiceLuar",
  enableReinitialize: true,
})(HeadServiceLuar);
export default connect((state) => {
  return {
    no_service_kirim: localStorage.getItem("kirim_service_luar_nomor"),
  };
})(HeadServiceLuar);
